<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <div class="">
            <el-form-item label="" style="margin-bottom: 5px">
              <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-input v-model="phone" auto-complete="off" placeholder=" " clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="name" auto-complete="off" placeholder=" " clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="agencyId" auto-complete="off" placeholder="ID" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
            </el-form-item>
          </div>

          <el-form-item label="">
            <el-cascader size="large" :options="provinceAndCityData" @change="handleChange" clearable></el-cascader>
          </el-form-item>
          <el-form-item>
            <el-select v-model="auditor" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in statusList" :key="item.UserName" :label="item.UserName" :value="item.UserName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="canRecommend" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in canRecommendList" :key="item.UserID" :label="item.UserName" :value="item.UserID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="settlementState" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in settlementStateList" :key="item.UserID" :label="item.UserName" :value="item.UserID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="IsNotParent" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in IsNotParentList" :key="item.UserID" :label="item.UserName" :value="item.UserID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="GetList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="AgencyRegisterDate" label="" width="180"></el-table-column>
        <el-table-column property="AgencyName" label=""></el-table-column>
        <el-table-column property="AgencyID" label="ID"></el-table-column>
        <el-table-column property="ParentName" label=""></el-table-column>
        <el-table-column property="ParentId" label="ID"></el-table-column>
        <el-table-column property="TrueName" label=""></el-table-column>
        <el-table-column property="OpenID" label="OpenID"></el-table-column>
        <el-table-column property="AgencyPhone" label="" width="180"></el-table-column>
        <el-table-column property="AgencyArea" label="" width="180"></el-table-column>
        <el-table-column property="QQ" label="QQ"></el-table-column>
        <el-table-column property="TotalEarning" label="()"></el-table-column>
        <el-table-column property="FromOneSub" label="()"></el-table-column>
        <el-table-column property="Manufacturer" label=""></el-table-column>
        <el-table-column property="FromSelfBusiness" label="()"></el-table-column>
        <el-table-column property="OneSub" label="">
          <template slot-scope="scope">
            <el-tag type="success" @click="OneSub(scope.row)" class="OneSub_div"> {{ scope.row.OneSub }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column property="Auditor" label=""></el-table-column>
        <el-table-column property="ContactText" label="" width="100">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p style="max-width:350px">{{ scope.row.ContactText }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium"></el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column property="ContactTime" label=""></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-dropdown @command="handleCommand">
              <el-button type="primary" size="small">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item :command="composeValue(1,scope.row)"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(2,scope.row)" v-if="!scope.row.AgencyStatus"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(3,scope.row)" v-else></el-dropdown-item>
                <el-dropdown-item :command="composeValue(4,scope.row)"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(5,scope.row)" v-if="!scope.row.CanRecommend"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(6,scope.row)" v-else></el-dropdown-item>
                <el-dropdown-item :command="composeValue(7,scope.row)" v-if="!scope.row.ExtractionSwitch"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(8,scope.row)" v-else></el-dropdown-item>
                <el-dropdown-item :command="composeValue(9,scope.row)" v-if="scope.row.SettlementState == 2"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(10,scope.row)" v-else></el-dropdown-item>
                <el-dropdown-item :command="composeValue(11,scope.row)" v-if="scope.row.Special == 1"></el-dropdown-item>
                <el-dropdown-item :command="composeValue(12,scope.row)" v-else></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
  </div>
  <!---->
  <el-dialog title="" :visible.sync="EditInformationCodeName" width="600px" @close='EditPrizeDialog'>
    <el-form :model="EditInformationList" :rules="rules" ref="EditInformationList" label-width="130px" class="demo-ruleForm">
      <el-form-item label="">
        <div class="">{{EditInformationList.AgencyAreaName||''}} </div>
      </el-form-item>
      <el-form-item label="" prop="Contacts">
        <el-cascader size="large" :options="provinceAndCityData" v-model="selectedOptions" @change="EdithandleChange" clearable></el-cascader>
      </el-form-item>
      <el-form-item label="ID" prop="ParentId">
        <el-input v-model="EditInformationList.ParentId" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Manufacturer">
        <el-input v-model="EditInformationList.Manufacturer" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="ContactText">
        <el-input type="textarea" v-model="EditInformationList.ContactText" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="EditInformationCodeName = false"> </el-button>
      <el-button type="primary" size="small" @click.native="EditInformationSubmit('EditInformationList')"> </el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import {
  provinceAndCityData,
  regionData,
  provinceAndCityDataPlus,
  regionDataPlus,
  CodeToText,
  TextToCode
} from 'element-china-area-data'
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'barbill',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      startTime: '',
      endTime: '',
      phone: null,
      name: null,
      agencyId: null,
      agencyArea: null,
      auditor: null,
      canRecommend: null,
      IsNotParent: null,
      settlementState: null,
      //
      CodeToText,
      TextToCode,
      provinceAndCityData,
      ModifyInformationList: {},
      //
      Datevalue: {},
      GetList: {},
      statusList: [],
      canRecommendList: [{
          UserName: '',
          UserID: 1
        },
        {
          UserName: '',
          UserID: 2
        }
      ],
      settlementStateList: [{
          UserName: '',
          UserID: 1
        },
        {
          UserName: '',
          UserID: 2
        }
      ],
      IsNotParentList: [{
          UserName: '',
          UserID: 1
        },
        {
          UserName: '',
          UserID: 2
        }
      ],
      //
      selectedOptions: [],
      EditInformationCodeName: false,
      EditInformationList: {
        AgencyId: '',
        AgencyAreaName: '',
        AgencyArea: '',
        ParentId: '',
        Manufacturer: '',
        ContactText: '',
      },
      rules: {
        ParentIdc: [{
            required: false,
            message: 'ID',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }
        ],
        Manufacturer: [{
            required: false,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }
        ],
        ContactText: [{
            required: false,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 200,
            message: ' 1  200 ',
            trigger: 'blur'
          }
        ],
      }
    }
  },
  activated() {
    this.agencyId = this.$route.query.parentId || null;
    this.getData();
    this.GetFzr();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    handleChange(arr) {
      console.log(this.CodeToText[arr[0]] + this.CodeToText[arr[1]]);
      this.agencyArea = this.CodeToText[arr[0]] + this.CodeToText[arr[1]];
      this.getData();
    },
    //
    EdithandleChange(arr) {
      console.log(this.CodeToText[arr[0]] + this.CodeToText[arr[1]]);
      this.EditInformationList.AgencyArea = this.CodeToText[arr[0]] + this.CodeToText[arr[1]];
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //
    GetFzr: function() {
      var that = this;
      this.vq.bindTo({
        url: "/api/CRM/GetFzr",
        type: 'get',
        loader: true,
        addfn: function(res) {
          that.statusList = res.Object;
          that.statusList.push({
            Phone: "",
            UserId: "",
            UserName: ""
          })
        }
      });
    },
    //tabel
    getData: function() {
      var that = this;
      var getArr = {
        startTime: this.startTime,
        endTime: this.endTime,
        phone: this.phone,
        name: this.name,
        agencyId: this.agencyId,
        agencyArea: this.agencyArea,
        auditor: this.auditor,
        canRecommend: this.canRecommend,
        IsNotParent: this.IsNotParent || 0,
        settlementState: this.settlementState || 0,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/Agency/GetList",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      });
    },
    //
    composeValue: function(index, row) {
      return {
        'index': index,
        'row': row
      }
    },
    handleCommand: function(command) {
      var index = command.index;
      var AgencyID = command.row.AgencyID;
      var _this = this;
      switch (index) {
        case 1:
          _this.ResetPwdCoom(AgencyID);
          break;
        case 2:
          _this.VerifyCoom(index, AgencyID);
          break;
        case 3:
          _this.VerifyCoom(index, AgencyID);
          break;
        case 4:
          _this.UpdateAgencyCoom(command.row);
          break;
        case 5:
          _this.SetShareCoom(index, AgencyID);
          break;
        case 6:
          _this.SetShareCoom(index, AgencyID);
          break;
        case 7:
          _this.ExtractionSwitch(index, AgencyID);
          break;
        case 8:
          _this.ExtractionSwitch(index, AgencyID);
        case 9:
          _this.RecommendationFee(index, AgencyID);
          break;
        case 10:
          _this.RecommendationFee(index, AgencyID);
          break;
        case 11:
          _this.SetSpecialFee(index, AgencyID);
          break;
        case 12:
          _this.SetSpecialFee(index, AgencyID);
          break;
      }
    },
    //
    ResetPwdCoom: function(AgencyID) {
      this.$confirm('', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var that = this;
        let parameterList = {
          agencyId: AgencyID
        };
        this.vq.sendMes({
          url: "/api/Agency/ResetPwd?agencyId=" + AgencyID,
          type: 'post',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: '',
              type: 'success'
            });

          }
        })
      }).catch(() => {});
    },
    //----
    VerifyCoom: function(index, AgencyID) {
      var that = this;
      if (index == 2) {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          state: 1
        };
      } else {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          state: 2
        };
      }
      this.$confirm('' + ounaem + '', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        this.vq.sendMes({
          url: "/api/Agency/Verify",
          type: 'get',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: ounaem + '',
              type: 'success'
            });
          }
        })
      }).catch(() => {});
    },
    //----
    ExtractionSwitch: function(index, AgencyID) {
      var that = this;
      if (index == 7) {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          type: 1
        };
      } else {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          type: 2
        };
      }
      this.$confirm('' + ounaem + '', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        this.vq.sendMes({
          url: "/api/Agency/ExtractionSwitch",
          type: 'get',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: ounaem + '',
              type: 'success'
            });
          }
        })
      }).catch(() => {});
    },
    // 1 2 ,
    RecommendationFee: function(index, AgencyID) {
      var that = this;
      if (index == 9) {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          type: 1
        };
      } else {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          type: 2
        };
      }
      this.$confirm('' + ounaem + '', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        this.vq.sendMes({
          url: "/api/Agency/SetSettlementState",
          type: 'get',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: '',
              type: 'success'
            });
          }
        })
      }).catch(() => {});
    },
    //
    SetSpecialFee: function(index, AgencyID) {
      var that = this;
      if (index == 11) {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          type: 2
        };
      } else {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          type: 1
        };
      }
      this.$confirm('' + ounaem + '', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        this.vq.sendMes({
          url: "/api/Agency/SetSpecial",
          type: 'get',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: ounaem + '',
              type: 'success'
            });
          }
        })
      }).catch(() => {});
    },
    //
    EditPrizeDialog: function() {
      this.$refs.EditInformationList.resetFields();
      this.EditInformationList.AgencyArea = "";
      this.EditInformationList.AgencyAreaName = "";
      this.selectedOptions = [];
    },
    UpdateAgencyCoom: function(row) {
      console.log(row);
      this.EditInformationCodeName = true;
      this.EditInformationList.AgencyId = row.AgencyID;
      this.EditInformationList.AgencyAreaName = row.AgencyArea;
      this.EditInformationList.ParentId = row.ParentId;
      this.EditInformationList.Manufacturer = row.Manufacturer;
      this.EditInformationList.ContactText = row.ContactText;
    },
    //
    EditInformationSubmit: function(EditInformationList) {
      if (!this.EditInformationList.AgencyArea) {
        this.EditInformationList.AgencyArea = this.EditInformationList.AgencyAreaName
      }
      this.$refs[EditInformationList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Agency/UpdateAgency",
            type: 'post',
            data: this.EditInformationList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.EditInformationCodeName = false;
              that.$refs[EditInformationList].resetFields();
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    SetShareCoom: function(index, AgencyID) {
      var that = this;
      if (index == 5) {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          state: true
        };
      } else {
        var ounaem = "";
        var parameterList = {
          agencyId: AgencyID,
          state: false
        };
      }
      var getUrl = "/api/Agency/SetShare?agencyId=" + parameterList.agencyId + "&state=" + parameterList.state;
      this.$confirm('' + ounaem + '', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        this.vq.sendMes({
          url: getUrl,
          type: 'post',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: ounaem + '',
              type: 'success'
            });
          }
        })
      }).catch(() => {});
    },
    OneSub: function(row) {
      this.$router.push({
        path: '/RefereeList',
        query: {
          parentId: row.AgencyID
        }
      });
    },
  }
}
</script>



