<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
          </el-form-item>
          <!-- <el-form-item style="width:10%">
            <el-input v-model="netbarName" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item> -->
          <el-form-item style="width:10%">
            <el-input v-model="netbarId" auto-complete="off" placeholder="ID" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <!-- <el-form-item style="width:10%">
            <el-input v-model="agencyName" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item> -->
          <el-form-item style="width:10%">
            <el-input v-model="agencyId" auto-complete="off" placeholder="ID" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="state" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in netbarList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="isVerify" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="Auditor" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in AuditorList" :key="item.UserName" :label="item.UserName" :value="item.UserName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <el-tabs v-model="isRemove" type="card" @tab-click="handleClick">
        <el-tab-pane label="" name="0">
          <el-table :data="GetNetbarStateCensusList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
            <el-table-column property="NetbarName" label="" width="180"></el-table-column>
            <el-table-column property="NetbarId" label="ID"></el-table-column>
            <el-table-column property="AgencyName" label=""></el-table-column>
            <el-table-column property="AgencyId" label="ID"></el-table-column>
            <el-table-column property="Auditor" label=""></el-table-column>
            <el-table-column prop="State" label="" align='center' width='120' filter-placement="bottom-end">
              <template slot-scope="scope">
                <el-tag :type="scope.row.State == '0' ? 'danger':scope.row.State == '1' ? 'success':''" close-transition>
                  {{scope.row.State == "0" ? "":scope.row.State == "1" ? "":''}}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column property="CurrentDate" label="" width="180"></el-table-column>
            <el-table-column property="Operator" label=""></el-table-column>
            <el-table-column property="OperatorTime" label="" width="180"></el-table-column>
            <el-table-column property="Reason" label=""></el-table-column>
            <el-table-column label="" width="220">
              <template slot-scope="scope">
                <el-button type="success" size="small" @click.stop="Verifyformation(scope.$index, scope.row)" v-if="scope.row.IsVerify == 0"></el-button>
                <el-button size="small" disabled v-else></el-button>
                <el-button type="danger" size="small" @click.stop="deletInformation(scope.$index, scope.row,1)" v-if="scope.row.IsVerify != 0"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="end" style="padding:20px 0; ">
            <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="1">
          <el-table :data="GetNetbarStateCensusList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
            <el-table-column property="NetbarName" label="" width="180"></el-table-column>
            <el-table-column property="NetbarId" label="ID"></el-table-column>
            <el-table-column property="AgencyName" label=""></el-table-column>
            <el-table-column property="AgencyId" label="ID"></el-table-column>
            <el-table-column prop="State" label="" align='center' width='120' filter-placement="bottom-end">
              <template slot-scope="scope">
                <el-tag :type="scope.row.State == '0' ? 'danger':scope.row.State == '1' ? 'success':''" close-transition>
                  {{scope.row.State == "0" ? "":scope.row.State == "1" ? "":''}}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column property="CurrentDate" label="" width="180"></el-table-column>
            <el-table-column property="Operator" label="" width="180"></el-table-column>
            <el-table-column property="OperatorTime" label=""></el-table-column>
            <el-table-column property="Reason" label=""></el-table-column>
            <el-table-column label="" width="100">
              <template slot-scope="scope">
                <!-- <el-button type="success" size="small" @click.stop="Verifyformation(scope.$index, scope.row)" v-if="scope.row.IsVerify == 0"></el-button>
                <el-button size="small" disabled v-else></el-button> -->
                <el-button type="warning" size="small" @click.stop="deletInformation(scope.$index, scope.row,0)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="end" style="padding:20px 0; ">
            <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
          </el-row>
        </el-tab-pane>
      </el-tabs>


    </div>
    <!---->
    <el-dialog title="" :visible.sync="VerifyformationCodeName" width="500px" @close='VerifyformationDialog'>
      <el-form :model="VerifyformationList" :rules="rules" ref="VerifyformationList" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="Reason">
          <el-input type="textarea" v-model="VerifyformationList.Reason" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="VerifyformationCodeName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="VerifyInformationSubmit('VerifyformationList')"> </el-button>
      </div>
    </el-dialog>

  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'StateStatistics',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      startTime: '',
      endTime: '',
      netbarName: null,
      netbarId: null,
      agencyName: null,
      agencyId: null,
      state: '0',
      isVerify: '',
      isRemove: '0',
      Auditor:'',
      Datevalue: {},
      //
      VerifyformationCodeName: false,
      VerifyformationList: {
        Reason: null,
        NetbarId: null
      },
      GetNetbarStateCensusList: {},
      AuditorList:[],//
      netbarList: [{ //
        id: '0',
        name: ''
      }, {
        id: '1',
        name: ''
      }],
      statusList: [{ //
        id: '0',
        name: ''
      }, {
        id: '1',
        name: ''
      }],
      rules: {
        Reason: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
      }
    }
  },
  created() {
    this.getData();
    this.GetFzr();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    handleClick(tab, event) {
      // console.log(tab, event);
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //
    GetFzr: function() {
      var that = this;
      this.vq.bindTo({
        url: "/api/CRM/GetFzr",
        type: 'get',
        loader: true,
        addfn: function(res) {
          that.AuditorList = res.Object;
          that.AuditorList.push({
            Phone: "",
            UserId: "",
            UserName: ""
          })
        }
      });
    },
    //tabel
    getData: function() {
      var that = this;
      var getArr = {
        startTime: this.startTime,
        endTime: this.endTime,
        netbarName: this.netbarName,
        netbarId: this.netbarId,
        agencyName: this.agencyName,
        agencyId: this.agencyId,
        state: this.state,
        isVerify: this.isVerify,
        isRemove: this.isRemove,
        auditor:this.Auditor,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/NetbarState/GetNetbarStateCensusList",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      })
    },
    //
    deletInformation: function(index, row, type) {
      var NetbarId = row.NetbarId;
      var text = null;
      if (type == 1) {
        text = ''
      } else {
        text = ''
      }
      this.$confirm('' + text + '', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var that = this;
        let parameterList = {
          NetbarId: row.NetbarId,
          IsRemove: type,
        };
        this.vq.sendMes({
          url: "/api/NetbarState/RemoveNetbar",
          type: 'post',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: text + '',
              type: 'success'
            });

          }
        })
      }).catch(() => {});
    },
    VerifyformationDialog: function() {
      this.$refs.VerifyformationList.resetFields();
    },
    Verifyformation: function(index, row) {
      this.VerifyformationCodeName = true;
      this.VerifyformationList.NetbarId = row.NetbarId;
    },
    VerifyInformationSubmit: function(VerifyformationList) {
      this.$refs[VerifyformationList].validate((valid) => {
        if (valid) {
          var that = this;
          let parameterList = {
            NetbarId: this.VerifyformationList.NetbarId,
            Reason: this.VerifyformationList.Reason,
          };
          this.vq.sendMes({
            url: "/api/NetbarState/Verify",
            type: 'post',
            data: parameterList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.VerifyformationCodeName = false;
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
  }
}
</script>



