<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="GameBetStatistics.Object" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="Id" sortable label="ID"></el-table-column>
        <el-table-column property="MaxTitle" label=""></el-table-column>
        <el-table-column property="StakeCount" sortable label=""></el-table-column>
        <el-table-column property="SuccessCount" sortable label=""></el-table-column>
        <el-table-column property="FailCount" sortable label=""></el-table-column>
        <el-table-column property="IntegralSuccessSum" label=""></el-table-column>
        <el-table-column property="IntegralFailSum" sortable label=""></el-table-column>
        <el-table-column property="ConsumerCompleteCount" sortable label=""></el-table-column>
        <el-table-column property="ConsumerCount" sortable label=""></el-table-column>
      </el-table>
      </el-card>
    </div>
  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'QuizStatistics',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      phone: null,
      consumerId: null,
      netbarId: null,
      startTime: '',
      endTime: '',
      Datevalue: {},
      GameBetStatistics: {}
    }
  },
  created() {
    this.getData();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //tabel
    getData: function() {
      var that = this;
      var StateValue = null;
      if (this.state == '') {
        StateValue = '-1';
      } else {
        StateValue = this.state;
      }
      var getArr = {
        type: null,
        orderby: null,
        startTime: this.startTime,
        endTime: this.endTime,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/BetRelevant/GameBetStatistics",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {}
      })
    }
  }
}
</script>
<style media="screen">
.el-table .caret-wrapper {
  padding: 0;
}
</style>



