<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span>  </span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" :clearable="true"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-input v-model="gameName" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="tableList" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="GameName" label=""></el-table-column>
        <el-table-column property="OrderTimeStr" label=""></el-table-column>
        <el-table-column property="Count" label=""></el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'GameInventory',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      gameName: null,
      startTime: '',
      endTime: '',
      Datevalue: {},
      tableList: []
    }
  },
  created() {
    this.getData();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //tabel
    getData: function() {
      var that = this;
      var getArr = {
        gameName: this.gameName,
        startTime: this.startTime,
        endTime: this.endTime,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/SteamOrder/GetOrderErrorStatistics",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.tableList = res.Items;
          that.total = res.Total;
        }
      })
    }
  }
}
</script>



