<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-select v-model="isShow" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
          <el-row style="margin-bottom: 10px;">
            <el-button type="primary" icon="el-icon-circle-plus-outline" size="medium" plain @click="AddMessageName=true"></el-button>
          </el-row>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="GetMessageList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="SendDate" label="" width="180"></el-table-column>
        <el-table-column property="Title" label=""></el-table-column>
        <el-table-column property="IsShow" label="" :formatter="formatIsShow"></el-table-column>
        <el-table-column property="ExpirationDate" label=""></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.IsShow" active-color="#00A854" :active-value="1" inactive-color="#cccccc" :inactive-value="0" @change="changeSwitch(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click.stop="detailsInformation(scope.$index, scope.row)"></el-button>
            <el-button type="danger" size="small" @click.stop="deletInformation(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
    <!--POST /api/Message/AddMessage  -->
    <el-dialog title="" :visible.sync="AddMessageName" width="600px" @close='SetPrizeDialog'>
      <el-form :model="AddMessageList" :rules="rules" ref="AddMessageList" label-width="120px" class="demo-ruleForm">
        <el-form-item label="" prop="Title">
          <el-input v-model="AddMessageList.Title" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="IsShow">
          <el-switch v-model="AddMessageList.IsShow" active-color="#00A854" :active-value="1" inactive-color="#cccccc" :inactive-value="0"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="ExpirationDate">
          <el-date-picker v-model="AddMessageList.ExpirationDate" type="date" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="Content">
          <el-input type="textarea" v-model="AddMessageList.Content" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="AddMessageName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="AddMessageSubmit('AddMessageList')"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="centerDialogVisible" width="600px" class="CreateMessage">
      <h3 class="CreateMessage_title">{{CreateMessageList.Title}}</h3>
      <div class="CreateMessage_date">{{CreateMessageList.SendDate}} </div>
      <div class="CreateMessage_body">
        {{CreateMessageList.Content}}
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'barbill',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      isShow: '',
      startTime: '',
      endTime: '',
      Datevalue: {},
      //
      AddMessageName: false,
      AddMessageList: {
        MessageType: 1,
        Title: '',
        IsShow: 0,
        ExpirationDate: '',
        Content: ''
      },
      GetMessageList: {},
      //
      centerDialogVisible: false,
      CreateMessageList: {},
      statusList: [{ //
        id: '0',
        name: ''
      }, {
        id: '1',
        name: ''
      }, ],
      rules: {
        Title: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }
        ],
        IsShow: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        ExpirationDate: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        Content: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 300,
            message: ' 1  300 ',
            trigger: 'blur'
          }
        ],
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //tabel
    getData: function() {
      var that = this;
      var StateValue = null;
      if (this.state == '') {
        StateValue = '-1';
      } else {
        StateValue = this.state;
      }
      var getArr = {
        messageType: '1',
        startDate: this.startTime,
        endDate: this.endTime,
        isShow: this.isShow,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/Message/GetMessageList",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      })
    },
    //
    SetPrizeDialog: function() {
      this.$refs.AddMessageList.resetFields();
    },
    //
    AddMessageSubmit: function(AddMessageList) {
      this.AddMessageList.ExpirationDate = this.$moment(this.AddMessageList.ExpirationDate).format("YYYY-MM-DD") + "T00:00:00";
      this.$refs[AddMessageList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Message/AddMessage",
            type: 'post',
            data: this.AddMessageList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.AddMessageName = false;
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    deletInformation: function(index, row) {
      this.$confirm('', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var that = this;
        let parameterList = {
          Id: row.ID,
        };
        this.vq.sendMes({
          url: "/api/Message/DeleteMessage",
          type: 'post',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: '',
              type: 'success'
            });

          }
        })
      }).catch(() => {});
    },
    //
    changeSwitch: function(row) {
      var that = this;
      var paramList = {
        Id: row.ID,
        MessageType: '1',
      };
      this.vq.sendMes({
        url: "/api/Message/ShowMessage",
        type: 'post',
        data: paramList,
        loader: true,
        fn: function(res) {
          that.getData();
        }
      })
    },
    //
    detailsInformation: function(index, row) {
      this.CreateMessageList = row;
      this.centerDialogVisible = true;
    },
    //
    formatIsShow: function(row, column) {
      return row.IsShow == 0 ? '' : row.IsShow == 1 ? '' : '';
    },
  }
}
</script>
<style media="screen">

</style>



