<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="phone" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="NetbarId" auto-complete="off" placeholder="ID" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="type" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in typeList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="state" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="GetProposalList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="CreateTime" label="" width="180"></el-table-column>
        <el-table-column property="ConsumerId" label="ID"></el-table-column>
        <el-table-column property="NetbarId" label="ID"></el-table-column>
        <el-table-column property="Phone" label=""></el-table-column>
        <el-table-column property="Type" label=""></el-table-column>
        <el-table-column property="Content" label=""></el-table-column>
        <el-table-column property="OrderNumber" label=""></el-table-column>
        <el-table-column property="HandleState" label=""></el-table-column>
        <el-table-column property="HandleTime" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button type="success" size="small" @click.stop="HandleInformation(scope.$index, scope.row)" v-if="scope.row.HandleState == ''"></el-button>
            <el-button type="danger" size="small" @click.stop="deleteInformation(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
  </div>
  <!-- -->
  <el-dialog title="" :visible.sync="SendMessageName" width="800px" @close='SendMessageDialog'>
    <el-form :model="SendMessageList" :rules="rules" ref="SendMessageList" label-width="120px" class="demo-ruleForm">
      <el-row :gutter="10">
        <el-col :span="24">
          <el-form-item label="" prop="Title">
            <el-input v-model="SendMessageList.Title" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="">
            <span></span>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="Content">
            <el-input type="textarea" v-model="SendMessageList.Content" auto-complete="off" placeholder="" :autosize="{ minRows: 8, maxRows: 14}"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="SendMessageName = false"> </el-button>
      <el-button type="primary" size="small" @click.native="SendMessageSubmit('SendMessageList')"> </el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'Feedback',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      name: null,
      NetbarId: null,
      phone: null,
      type: '',
      state: '',
      startTime: '',
      endTime: '',
      Datevalue: {},
      //
      SendMessageName: false,
      SendMessageList: {},
      //
      EditInformationCodeName: false,
      EditInformationList: {
        id: null,
        Contacts: null,
        Phone: null,
        QQ: null,
        Ratio: null
      },
      GetProposalList: {},
      typeList: [{ //
        id: 'steam',
        name: 'steam'
      }, {
        id: '',
        name: ''
      }, {
        id: '',
        name: ''
      }],
      statusList: [{ //
        id: '0',
        name: ''
      }, {
        id: '1',
        name: ''
      }],
      rules: {
        Title: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }
        ],
        Content: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 200,
            message: ' 1  200 ',
            trigger: 'blur'
          }
        ],
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //tabel
    getData: function() {
      var that = this;
      var getArr = {
        name: this.name,
        netbarId: this.netbarId,
        type: this.type,
        phone: this.phone,
        state: this.state,
        State: this.state,
        startTime: this.startTime,
        endTime: this.endTime,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/Proposal/GetProposalList",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      })
    },
    //
    deleteInformation: function(index, row) {
      this.$confirm('', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var that = this;
        let parameterList = {
          id: row.Id,
        };
        this.vq.sendMes({
          url: "/api/Proposal/PropsalDeleteById?id=" + row.Id,
          type: 'post',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: '',
              type: 'success'
            });

          }
        })
      }).catch(() => {});
    },
    HandleInformation: function(index, row) {
      this.$confirm('', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var that = this;
        let parameterList = {
          id: row.Id,
        };
        this.vq.sendMes({
          url: "/api/Proposal/UpdateHandlState?id=" + row.Id,
          type: 'post',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.SendMessage(row);
          }
        })
      }).catch(() => {});

    },
    // BoxHandleInformation: function(ConsumerId) {
    //   this.$confirm('', '', {
    //     cancelButtonText: '',
    //     confirmButtonText: '',
    //     type: 'success'
    //   }).then(() => {
    //     this.$router.push({
    //       path: '/UserMessageManagement',
    //       query: {
    //         ProposalId: ConsumerId
    //       }
    //     });
    //   }).catch(() => {});
    // },
    //
    SendMessage: function(row) {
      this.SendMessageName = true;
      this.SendMessageList.ConsumerId = row.ConsumerId;
      this.SendMessageList.ProposalId = row.Id;
    },
    SendMessageDialog: function() {
      this.$refs.SendMessageList.resetFields();
    },
    SendMessageSubmit: function(SendMessageList) {
      this.$refs[SendMessageList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Consumer/ConsumerMsgCreate",
            type: 'post',
            data: this.SendMessageList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.SendMessageName = false;
              that.$refs[SendMessageList].resetFields();
              that.$message({
                message: '',
                type: 'success'
              });
              that.$router.push({
                path: '/UserMessageManagement',
                query: {
                  ConsumerId: that.SendMessageList.ConsumerId
                }
              });
            }
          })
        } else {
          return false;
        }
      });
    },
  }
}
</script>



