<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-row style="margin-bottom: 10px;">
          <el-button type="primary" size="medium" plain @click="addCreateLowerHair"></el-button>
        </el-row>
      </div>
      <!--  -->
      <el-table :data="GetLowerHairList" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="Id" label="ID" width="180"></el-table-column>
        <el-table-column property="Name" label=""></el-table-column>
        <el-table-column property="StartupTime" label=""></el-table-column>
        <el-table-column property="CreateTime" label=""></el-table-column>
        <el-table-column property="ExpireTime" label=""></el-table-column>
        <el-table-column prop="Type" label="" align='center' filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag :type="scope.row.Type == '1' ? 'danger':scope.row.Type == '2' ? 'success':scope.row.Type == '3' ? 'primary':''" close-transition>
              {{scope.row.Type == "1" ? "":scope.row.Type == "2" ? "":scope.row.Type == "3" ? "":''}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column property="Url" label="URL"></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click.stop="EditCreateLowerHair(scope.$index, scope.row)"></el-button>
            <el-button type="danger" size="small" @click.stop="DeleteCreateLowerHair(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      </el-card>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="addCreateLowerHairCodeName" width="700px" @close='addCreateLowerHairDialog'>
      <el-form :model="addCreateLowerHairList" :rules="rules" ref="addCreateLowerHairList" label-width="150px" class="demo-ruleForm">
        <el-form-item label="" prop="Name">
          <el-input v-model="addCreateLowerHairList.Name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ExpireTime">
          <el-date-picker v-model="addCreateLowerHairList.ExpireTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-row :gutter="20">
          <el-col :span="20">
            <div class="grid-content bg-purple">
              <el-form-item label="" prop="StartupTime">
                <el-input v-model="addCreateLowerHairList.StartupTime" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content bg-purple" style="line-height: 38px;">
              (  )
            </div>
          </el-col>
        </el-row>
        <el-form-item label="" prop="Type">
          <el-select v-model="addCreateLowerHairList.Type" placeholder="" clearable>
            <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ExePath">
          <el-input v-model="addCreateLowerHairList.ExePath" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="Url">
          <el-input v-model="addCreateLowerHairList.Url" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="addCreateLowerHairCodeName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="addCreateLowerHairSubmit('addCreateLowerHairList')"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="EditCreateLowerHairCodeName" width="700px" @close='EditCreateLowerHairDialog'>
      <el-form :model="EditCreateLowerHairList" :rules="rules" ref="EditCreateLowerHairList" label-width="150px" class="demo-ruleForm">
        <el-form-item label="" prop="Name">
          <el-input v-model="EditCreateLowerHairList.Name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ExpireTime">
          <el-date-picker v-model="EditCreateLowerHairList.ExpireTime" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-row :gutter="20">
          <el-col :span="20">
            <div class="grid-content bg-purple">
              <el-form-item label="" prop="StartupTime">
                <el-input v-model="EditCreateLowerHairList.StartupTime" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content bg-purple" style="line-height: 38px;">
              (  )
            </div>
          </el-col>
        </el-row>
        <el-form-item label="" prop="Type">
          <el-select v-model="EditCreateLowerHairList.Type" placeholder="" clearable>
            <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ExePath">
          <el-input v-model="EditCreateLowerHairList.ExePath" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="Url">
          <el-input v-model="EditCreateLowerHairList.Url" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="EditCreateLowerHairCodeName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="EditCreateLowerHairSubmit('EditCreateLowerHairList')"> </el-button>
      </div>
    </el-dialog>
  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'ValueAddedSettings',
  components: {
    tabbar
  },
  data: function() {
    return {
      //
      addCreateLowerHairCodeName: false,
      addCreateLowerHairList: {},
      addDatevalue: {},
      //
      EditCreateLowerHairCodeName: false,
      EditCreateLowerHairList: {
        Id: null,
        Name: null,
        Url: null,
        ExePath: null,
        StartupTime: null,
        Type: null,
        CreateTime: null,
        ExpireTime: null
      },
      GetLowerHairList: [],
      statusList: [{ //
        id: 1,
        name: ''
      }, {
        id: 2,
        name: ' '
      }, {
        id: 3,
        name: ' '
      }],
      rules: {
        Name: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 20,
            message: ' 1  20 ',
            trigger: 'blur'
          }
        ],
        Url: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        ExePath: [{
          required: true,
          message: ' ',
          trigger: 'blur'
        }],
        StartupTime: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        Type: [{
          required: true,
          message: ' ,',
          trigger: 'change'
        }],
        ExpireTime: [{
          required: true,
          message: ' ,',
          trigger: 'change'
        }],
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    //tabel
    getData: function() {
      var that = this;
      this.vq.bindTo({
        url: "/api/Netbar/GetLowerHairList",
        type: 'get',
        loader: true,
        addfn: function(res) {
          that.GetLowerHairList = res.Object;
          that.total = res.Total;
        }
      })
    },
    //
    addCreateLowerHair: function() {
      this.addCreateLowerHairCodeName = true;
    },
    addCreateLowerHairDialog: function() {
      // this.$refs.addCreateLowerHairList.resetFields();
      this.addCreateLowerHairList = {};
    },
    addCreateLowerHairSubmit: function(addCreateLowerHairList) {
      this.$refs[addCreateLowerHairList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Netbar/CreateLowerHair",
            type: 'post',
            data: this.addCreateLowerHairList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.addCreateLowerHairCodeName = false;
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    EditCreateLowerHairDialog: function() {
      this.$refs.EditCreateLowerHairList.resetFields();
      this.EditCreateLowerHairList.ExpireTime = '';
    },
    EditCreateLowerHair: function(index, row) {
      this.EditCreateLowerHairCodeName = true;
      this.EditCreateLowerHairList.Id = row.Id;
      this.EditCreateLowerHairList.Name = row.Name || null;
      this.EditCreateLowerHairList.Url = row.Url;
      this.EditCreateLowerHairList.ExePath = row.ExePath;
      this.EditCreateLowerHairList.Type = row.Type;
      this.EditCreateLowerHairList.StartupTime = row.StartupTime;
      this.EditCreateLowerHairList.ExpireTime = row.ExpireTime;
      console.log(this.EditCreateLowerHairList);
    },
    EditCreateLowerHairSubmit: function(EditCreateLowerHairList) {
      this.$refs[EditCreateLowerHairList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Netbar/UpdateLowerHair",
            type: 'post',
            data: this.EditCreateLowerHairList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.EditCreateLowerHairCodeName = false;
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    DeleteCreateLowerHair: function(index,row) {
      this.$confirm('', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var that = this;
        let parameterList = {
          lowerHairId: row.Id,
        };
        this.vq.sendMes({
          url: "/api/Netbar/DeleteLowerHair",
          type: 'get',
          data: parameterList,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: '',
              type: 'success'
            });

          }
        })
      }).catch(() => {});
    }
  }
}
</script>



