<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
        <el-row style="margin-bottom: 10px;">
          <el-button type="primary" icon="el-icon-circle-plus-outline" size="medium" plain @click="AddedVersionNamebox"></el-button>
        </el-row>
      </el-form>
      <!--  -->
      <el-table :data="GetClientVersionList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="VersionNumber" label="" width="180"></el-table-column>
        <!-- <el-table-column property="VersionInfos" label=""></el-table-column> -->
        <el-table-column prop="State" label="" align='center' width='120' filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-button type="success" size="small" @click.stop="VersionInformationbox(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
        <el-table-column property="CreateTime" label=""></el-table-column>
        <el-table-column prop="State" label="" align='center' width='120' filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag :type="scope.row.State == '1' ? 'success':scope.row.State == '2' ? 'danger':''" close-transition>
              {{scope.row.State == "1" ? "":scope.row.State == "2" ? "":''}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click.stop="EditorialVersionbox(scope.$index, scope.row)" v-if="scope.row.State != 2"></el-button>
            <el-button type="success" size="small" @click.stop="OfficialRelease(scope.$index, scope.row)" v-if="scope.row.State != 2"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="VersionInformationName">
      <div class="">
        <p v-for="(item,index) in VersionInfosList"  :key="index"> {{item}}</p>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="VersionInformationName = false">   </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="AddedVersionName" width="800px" @close='AddedVersionDialog'>
      <el-form :model="AddedVersionList" :rules="rules" ref="AddedVersionList" label-width="140px" class="demo-ruleForm">
        <el-form-item label="" prop="VersionNumber">{{VersionNumber}}</el-form-item>
        <el-form-item label="" prop="VersionNumber">
          <el-input v-model="AddedVersionList.VersionNumber" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="VersionInfos">
          <el-input type="textarea" v-model="AddedVersionList.VersionInfos" :autosize="{ minRows: 12, maxRows: 12}" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="AddedVersionName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="AddedVersionSubmit('AddedVersionList')"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="EditorialVersionName" width="800px" @close='EditorialVersionDialog'>
      <el-form :model="EditorialVersionList" :rules="rules" ref="EditorialVersionList" label-width="140px" class="demo-ruleForm">
        <el-form-item label="" prop="VersionNumber">{{VersionNumber}}</el-form-item>
        <el-form-item label="" prop="VersionNumber">
          <el-input v-model="EditorialVersionList.VersionNumber" auto-complete="off" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="VersionInfos">
          <el-input type="textarea" v-model="EditorialVersionList.VersionInfos" :autosize="{ minRows: 12, maxRows: 12}" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="EditorialVersionName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="EditorialVersionSubmit('EditorialVersionList')"> </el-button>
      </div>
    </el-dialog>

  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'VersionManagement',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      GetClientVersionList: [],
      //
      AddedVersionName: false,
      AddedVersionList: {},
      //
      EditorialVersionName: false,
      EditorialVersionList: {
        VersionNumber: '',
        VersionInfos: ''
      },
      VersionNumber: '', //
      VersionInformationName: false,
      VersionInfosList: '',
      rules: {
        Contacts: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 20,
            message: ' 1  20 ',
            trigger: 'blur'
          }
        ],
        Phone: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            pattern: /^1[2|3|4|5|6|7|8|9][0-9]\d{8}$/,
            message: ''
          }
        ],
        Ratio: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            pattern: /^100$|^(\d|[1-9]\d)$/,
            message: '100100'
          }
        ],
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //tabel
    getData: function() {
      var that = this;
      var getArr = {
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/ClientVersion/GetClientVersionList",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      })
    },
    //
    VersionInformationbox: function(index, row) {
      this.VersionInformationName = true;
      this.VersionInfosList = JSON.parse(row.VersionInfos);
    },
    //
    GetNewestClientVersionFun: function() {
      var that = this;
      this.vq.bindTo({
        url: "/api/ClientVersion/GetNewestClientVersion",
        type: 'get',
        data: {},
        loader: true,
        addfn: function(res) {
          that.VersionNumber = res.Object.VersionNumber;
          console.log(res.Object.VersionNumber);
        }
      })
    },
    //
    AddedVersionNamebox: function() {
      this.AddedVersionName = true;
      this.GetNewestClientVersionFun();
    },
    AddedVersionDialog: function() {
      this.$refs.AddedVersionList.resetFields();
    },
    AddedVersionSubmit: function(AddedVersionList) {
      // var VersionInfosJson = JSON.parse(this.AddedVersionList.VersionInfos);
      this.$refs[AddedVersionList].validate((valid) => {
        if (valid) {
          var getArr = {
            VersionNumber: this.AddedVersionList.VersionNumber,
            VersionInfos: this.AddedVersionList.VersionInfos,
            State: '',
            Remark: '',
            XmlUrl: ''
          };
          var _data = this.unit.getFinal.call(this, getArr, true);
          var that = this;
          this.vq.sendMes({
            url: "/api/ClientVersion/CreateVersion",
            type: 'post',
            data: _data,
            loader: true,
            fn: function(res) {
              that.getData();
              that.AddedVersionName = false;
              that.$refs.AddedVersionList.resetFields();
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    EditorialVersionbox: function(index, row) {
      this.EditorialVersionList.VersionNumber = row.VersionNumber;
      this.EditorialVersionList.VersionInfos = row.VersionInfos;
      this.GetNewestClientVersionFun();
      this.EditorialVersionName = true;
    },
    EditorialVersionDialog: function() {
      this.EditorialVersionList.VersionNumber = '';
      this.EditorialVersionList.VersionInfos = '';
      this.$refs.EditorialVersionList.resetFields();
    },
    EditorialVersionSubmit: function(EditorialVersionList) {
      this.$refs[EditorialVersionList].validate((valid) => {
        if (valid) {
          var getArr = {
            VersionNumber: this.EditorialVersionList.VersionNumber,
            VersionInfos: this.EditorialVersionList.VersionInfos,
            State: '',
            Remark: '',
            XmlUrl: ''
          };
          var _data = this.unit.getFinal.call(this, getArr, true);
          var that = this;
          this.vq.sendMes({
            url: "/api/ClientVersion/CreateVersion",
            type: 'post',
            data: _data,
            loader: true,
            fn: function(res) {
              that.getData();
              that.EditorialVersionName = false;
              that.$refs.EditorialVersionList.resetFields();
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    OfficialRelease: function(index, row) {
      this.$confirm('', '', {
        cancelButtonText: '',
        confirmButtonText: '',
        type: 'warning'
      }).then(() => {
        var getArr = {
          VersionNumber: row.VersionNumber,
          VersionInfos: row.VersionInfos,
          State: 2,
          Remark: '',
          XmlUrl: ''
        };
        var _data = this.unit.getFinal.call(this, getArr, true);
        var that = this;
        this.vq.sendMes({
          url: "/api/ClientVersion/CreateVersion",
          type: 'post',
          data: _data,
          loader: true,
          fn: function(res) {
            that.getData();
            that.$message({
              message: '',
              type: 'success'
            });
          }
        })
      }).catch(() => {});
    }
  }
}
</script>



