<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="phone" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="consumerId" auto-complete="off" placeholder="ID" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="state" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item>
            <el-select v-model="isAuthorize" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in isAuthorizeList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="danger" @click="batchSendMessage"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="GetConsumerMsgList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="ConsumerId" label="ID" width="120"></el-table-column>
        <el-table-column property="Phone" label=""></el-table-column>
        <el-table-column property="SendTime" label=""></el-table-column>
        <el-table-column property="Title" label=""></el-table-column>
        <el-table-column property="ReadState" label=""></el-table-column>
        <el-table-column property="Type" label=""></el-table-column>
        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <el-button type="success" size="small" @click.stop="detailsInformation(scope.$index, scope.row)"></el-button>
            <el-button type="danger" size="small" @click.stop="SendMessage(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
  </div>
  <!--  -->
  <el-dialog title="" :visible.sync="centerDialogVisibleName" width="600px" class="CreateMessage">
    <h3 class="CreateMessage_title">{{CreateMessageList.Title}}</h3>
    <div class="CreateMessage_date">{{CreateMessageList.SendTime}} </div>
    <div class="CreateMessage_body">
      {{CreateMessageList.Content}}
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="centerDialogVisibleName = false"> </el-button>
    </span>
  </el-dialog>
  <!-- -->
  <el-dialog title="" :visible.sync="SendMessageName" width="800px" @close='SendMessageDialog'>
    <el-form :model="SendMessageList" :rules="rules" ref="SendMessageList" label-width="120px" class="demo-ruleForm">
      <el-row :gutter="10">
        <el-col :span="24">
          <el-form-item label="" prop="Title">
            <el-input v-model="SendMessageList.Title" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="">
            <span></span>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="Content">
            <el-input type="textarea" v-model="SendMessageList.Content" auto-complete="off" placeholder="" :autosize="{ minRows: 8, maxRows: 14}"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="SendMessageName = false"> </el-button>
      <el-button type="primary" size="small" @click.native="SendMessageSubmit('SendMessageList')"> </el-button>
    </div>
  </el-dialog>
  <!-- -->
  <el-dialog title="" :visible.sync="batchSendMessageName" width="800px" @close='batchSendMessageDialog'>
    <el-form :model="batchSendMessageList" :rules="rules" ref="batchSendMessageList" label-width="120px" class="demo-ruleForm">
      <el-row :gutter="10">
        <el-col :span="24">
          <el-form-item label="" prop="Title">
            <el-input v-model="batchSendMessageList.Title" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="">
            <span></span>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="Content">
            <el-input type="textarea" v-model="batchSendMessageList.Content" auto-complete="off" placeholder="" :autosize="{ minRows: 8, maxRows: 14}"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="batchSendMessageName = false"> </el-button>
      <el-button type="primary" size="small" @click.native="batchSendMessageSubmit('batchSendMessageList')"> </el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'UserMessageManagement',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      phone: null,
      consumerId: null,
      state: '',
      isAuthorize: null,
      startTime: '',
      endTime: '',
      Datevalue: {},
      GetConsumerMsgList: {},
      CreateMessageList: {}, //
      centerDialogVisibleName: false,
      statusList: [{ //
          id: '1',
          name: ''
        },
        {
          id: '2',
          name: ''
        }
      ],
      //
      SendMessageName: false,
      SendMessageList: {},
      //
      batchSendMessageName: false,
      batchSendMessageList: {},
      rules: {
        Title: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 30,
            message: ' 1  30 ',
            trigger: 'blur'
          }
        ],
        Content: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 200,
            message: ' 1  200 ',
            trigger: 'blur'
          }
        ],
      }
    }
  },
  created() {
    this.consumerId = this.$route.query.ConsumerId || null;
    this.getData();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //tabel
    getData: function() {
      var that = this;
      var getArr = {
        phone: this.phone,
        consumerId: this.consumerId,
        state: this.state,
        isAuthorize: this.isAuthorize,
        startTime: this.startTime,
        endTime: this.endTime,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/Consumer/GetConsumerMsgList",
        type: 'get',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      })
    },
    //
    detailsInformation: function(index, row) {
      this.CreateMessageList = row;
      this.centerDialogVisibleName = true;
    },
    //
    SendMessage: function(index, row) {
      this.SendMessageName = true;
      this.SendMessageList.ConsumerId = row.ConsumerId;
    },
    SendMessageDialog: function() {
      this.$refs.SendMessageList.resetFields();
    },
    SendMessageSubmit: function(SendMessageList) {
      this.$refs[SendMessageList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Consumer/ConsumerMsgCreate",
            type: 'post',
            data: this.SendMessageList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.SendMessageName = false;
              that.$refs[SendMessageList].resetFields();
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    batchSendMessage: function() {
      this.batchSendMessageName = true;
    },
    batchSendMessageDialog: function() {
      this.$refs.batchSendMessageList.resetFields();
    },
    batchSendMessageSubmit: function(batchSendMessageList) {
      this.$refs[batchSendMessageList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Consumer/ConsumerMsgAllPush",
            type: 'post',
            data: this.batchSendMessageList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.batchSendMessageName = false;
              that.$refs[batchSendMessageList].resetFields();
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
  }
}
</script>



