<template>
<div class="wraper personinfo-wrap">
  <tabbar></tabbar>
  <div class="padding-pb fs-18 item-title">
    <span class="little-blue"></span>
    <span></span>
  </div>
  <div class="innerwrap">
    <div class="info-content">
      <!--  -->
      <div>
        <el-form :inline="true" class="demo-form-inline" style="padding: 0 5px">
          <el-form-item label="" style="margin-bottom: 5px">
            <el-date-picker v-model="Datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="" @change="DateChange" clearable></el-date-picker>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="NetbarId" auto-complete="off" placeholder="ID" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="NetbarName" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="AgencyPhone" auto-complete="off" placeholder="" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item style="width:10%">
            <el-input v-model="AgencyId" auto-complete="off" placeholder="id" clearable @clear="onSubmit" @keyup.enter.native="onSubmit"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="NetbarAccountNumberState" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in statusList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="IsActivation" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in IsActivationList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="Auditor" placeholder="" clearable @change="onSubmit">
              <el-option v-for="item in AuditorList" :key="item.UserName" :label="item.UserName" :value="item.UserName">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="pull-right">
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-form-item>
          <el-checkbox-group v-model="TypeList" @change="handleCheckedCitiesChange">
            <el-checkbox v-for="city in checkboxArr" :label="city.checkboxid" :key="city.checkboxid">{{city.checkboxname}}</el-checkbox>
          </el-checkbox-group>
        </el-form>
      </div>
      <!--  -->
      <el-table :data="GetNetbarList.Items" tooltip-effect="dark" border style="width: 100%" :header-cell-style="{background:'rgb(220, 223, 230)',color:'#333'}">
        <el-table-column property="NetbarID" label="Id" width="80"></el-table-column>
        <el-table-column property="NetbarName" label=""></el-table-column>
        <el-table-column property="CreateTime" label="" width="160"></el-table-column>
        <el-table-column property="AgencyName" label=""></el-table-column>
        <el-table-column property="AgencyPhone" label=""></el-table-column>
        <el-table-column property="AgencyId" label="ID"></el-table-column>
        <el-table-column property="AgencyId" label="">
          <template slot-scope="scope">
          <p>{{scope.row.ProName}}{{scope.row.CityName}}</p>
          </template>
        </el-table-column>
        <el-table-column property="Auditor" label=""></el-table-column>
        <el-table-column property="NetbarBusiness" label="" :formatter="formatNetbarBusiness" width="160"></el-table-column>
        <el-table-column property="ProductPowerInfos" label="" :formatter="formatProductPowerInfos" width="160"></el-table-column>
        <el-table-column property="IPs" label="IP"></el-table-column>
        <el-table-column property="ContactText" label="" width="130">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <div>
                <p>steam</p>
                <p><span style="color:#e60816">{{getViptime(scope.row.ProductPowerInfos,'steam')}} </span></p>
                <p></p>
                <p><span style="color:#e60816">{{getViptime(scope.row.ProductPowerInfos,'')}} </span></p>
              </div>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium"></el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <!-- <el-table-column label="" width="140">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.NetbarBusiness.IsNotShowAD" active-color="#00A854" :active-value="true" inactive-color="#cccccc" :inactive-value="false" @change="changeSwitch(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="130">
          <template slot-scope="scope">
            <p><span style="color:#e60816">{{scope.row.UpdatingVersion}} </span></p>
            <el-button type="primary" size="small" @click.stop="EditInformation(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column> -->
      </el-table>
      <el-row type="flex" justify="end" style="padding:20px 0; ">
        <el-pagination @current-change="handleCurrentChange" @size-change="sizeCurrentChange" :page-sizes="[10, 15, 20]" :page-size="10" :current-page="pageNo" layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
      </el-row>
      </el-card>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="EditInformationCodeName" width="500px" @close='EditPrizeDialog'>
      <el-form :model="EditInformationList" :rules="rules" ref="EditInformationList" label-width="150px" class="demo-ruleForm">
        <el-form-item label="" prop="version">
          <el-input v-model="EditInformationList.version" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="EditInformationCodeName = false"> </el-button>
        <el-button type="primary" size="small" @click.native="EditInformationSubmit('EditInformationList')"> </el-button>
      </div>
    </el-dialog>

  </div>
</div>
</template>

<script>
import tabbar from "../tabbar.vue";
import axios from "axios";
export default {
  name: 'barbill',
  components: {
    tabbar
  },
  data: function() {
    return {
      pageSize: 10,
      pageNo: 1,
      total: 0,
      //
      NetbarId: null,
      NetbarName: null,
      AgencyPhone: null,
      AgencyId: null,
      AgencyName: '',
      NetbarAccountNumberState: '',
      Type: '',
      IsActivation: '',
      Auditor: '',
      startTime: '',
      endTime: '',
      TypeList: [], //
      Datevalue: {},
      //
      EditInformationCodeName: false,
      EditInformationList: {
        version: null,
        NetbarID: null
      },
      GetNetbarList: {},
      //
      statusList: [{ //
          id: '1',
          name: ''
        }, {
          id: '2',
          name: ''
        },
        {
          id: '3',
          name: ''
        }
      ],
      //
      IsActivationList: [{ //
        id: '1',
        name: ''
      }, {
        id: '2',
        name: ''
      }],
      //
      AuditorList: [{
        Phone: "",
        UserId: "",
        UserName: ""
      }],
      checkboxArr: [{
        checkboxname: "580",
        checkboxid: 1,
        value: false
      }, {
        checkboxname: "580",
        checkboxid: 32,
        value: false
      }, {
        checkboxname: "",
        checkboxid: 2,
        value: false
      }, {
        checkboxname: "",
        checkboxid: 4,
        value: false
      }, {
        checkboxname: "",
        checkboxid: 8,
        value: false
      }, {
        checkboxname: "steam",
        checkboxid: 16,
        value: false
      }],
      rules: {
        version: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 20,
            message: ' 1  20 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getData();
    this.GetFzr();
  },
  methods: {
    //   pageSize 
    handleCurrentChange: function(val) {
      this.pageNo = val;
      this.getData();
    },
    // currentPage 
    sizeCurrentChange: function(val) {
      this.pageSize = val;
      this.getData();
    },
    onSubmit: function() {
      this.pageNo = 1;
      this.getData();
    },
    //
    DateChange: function() {
      if (this.Datevalue) {
        var date1 = this.Datevalue[0];
        var date2 = this.Datevalue[1];
        this.startTime = this.$moment(date1).format("YYYY-MM-DD");
        this.endTime = this.$moment(date2).format("YYYY-MM-DD");
        this.getData();
      } else {
        this.startTime = '';
        this.endTime = '';
        this.getData();
      }
    },
    //
    GetFzr: function() {
      var that = this;
      this.vq.bindTo({
        url: "/api/CRM/GetFzr",
        type: 'get',
        loader: true,
        addfn: function(res) {
          var GeegstatusList = res.Object;
          that.GeegstatusList = GeegstatusList;
          for (var i = 0; i < GeegstatusList.length; i++) {
            that.AuditorList.push(GeegstatusList[i]);
          }
        }
      });
    },
    handleCheckedCitiesChange(value) {
      console.log(value);
      var typeValue = 0;
      for (var i = 0; i < value.length; i++) {
        typeValue += value[i];
      };
      this.Type = typeValue;
      this.getData();
    },
    //tabel
    getData: function() {
      var that = this;
      var StateValue = null;
      if (this.NetbarAccountNumberState == '') {
        StateValue = '0';
      } else {
        StateValue = this.NetbarAccountNumberState;
      }
      var getArr = {
        StartTime: this.startTime + "T00:00:00",
        EndTime: this.endTime + "T00:00:00",
        NetbarId: this.NetbarId,
        NetbarName: this.NetbarName,
        AgencyPhone: this.AgencyPhone,
        AgencyId: this.AgencyId,
        AgencyName: this.AgencyName,
        NetbarAccountNumberState: StateValue,
        Type: this.Type,
        IsActivation: this.IsActivation,
        Auditor: this.Auditor,
        pageSize: this.pageSize,
        pageIndex: this.pageNo
      };
      var _data = this.unit.getFinal.call(this, getArr, true);
      this.vq.bindTo({
        url: "/api/Netbar/GetNetbarList",
        type: 'post',
        data: _data,
        loader: true,
        addfn: function(res) {
          that.total = res.Total;
        }
      })
    },
    //
    EditPrizeDialog: function() {
      this.$refs.EditInformationList.resetFields();
    },
    //
    EditInformation: function(index, row) {
      this.EditInformationCodeName = true;
      this.EditInformationList.NetbarID = row.NetbarID;
    },
    //
    EditInformationSubmit: function(EditInformationList) {
      this.$refs[EditInformationList].validate((valid) => {
        if (valid) {
          var that = this;
          this.vq.sendMes({
            url: "/api/Netbar/SetNetbarVersion",
            type: 'get',
            data: this.EditInformationList,
            loader: true,
            fn: function(res) {
              that.getData();
              that.EditInformationCodeName = false;
              that.$message({
                message: '',
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    //
    changeSwitch: function(data) {
      var that = this;
      console.log(data);
      var paramList = {
        isNotOpen: data.NetbarBusiness.IsNotShowAD ? 1 : 0,
        netbarId: data.NetbarID,
      };
      this.vq.sendMes({
        url: "/api/Netbar/SetNetbarAD",
        type: 'get',
        data: paramList,
        loader: true,
        fn: function(res) {
          that.getData();
        }
      })
    },
    getViptime: function(item, type) {
      var itemVip = false;
      var word = "";
      for (var i = 0; i < item.length; i++) {
        if (item[i].Name == type) {
          itemVip = item[i];
        }
      }
      if (!itemVip) {
        return ''
      } else {
        if (itemVip.IsExpire == '') {
          return itemVip.ExpireDateTime
        } else {
          return ''
        }
      }
    },
    //
    formatNetbarBusiness: function(item, column) {
      var str = "";
      if (item) {
        if (item.NetbarBusiness.IsNotOpenWindow) {
          str += '580  ';
        } else {
          str += '580  ';
        }
        if (item.NetbarBusiness.IsNotSetAppTableIcon) {
          str += '  ';
        }
        if (item.NetbarBusiness.IsNotSetHomePage) {
          str += '  ';
        }
      } else {

      }
      return str;
    },
    //
    formatProductPowerInfos: function(item, column) {
      var str = ""
      if (item.ProductPowerInfos.length == 0) {
        return "steam";
      } else {
        for (var i = 0; i < item.ProductPowerInfos.length; i++) {
          if (item.ProductPowerInfos[i].IsExpire != '') {
            str += item.ProductPowerInfos[i].Name;
          }
        }
        if (str == '') {
          return "steam";
        }
        return str;
      }
    },
  }
}
</script>



